body, html {
  font-family: sans-serif;
  font-size: 1rem;
}

.console {
  width: 100%;
  height: 10rem;
  position: relative;
  font-family: monospace;
  background: black;
  color: white;
  padding: 1rem;
  box-sizing: border-box;
}
.console .log-entry {
  opacity: 0.5;
}
.console .log-entry .color {
  font-weight: bold;
}
.console .log-entry .color.green {
  color: lime;
}
.console .log-entry .color.blue {
  color: cyan;
}
.console .log-entry:last-child {
  opacity: 1;
}
.console #text {
  position: absolute;
  bottom: 0px;
}

.gameview {
  display: flex;
  flex-direction: row;
}

.actions {
  width: clamp(20rem, 20%, 100%);
  display: inline-block;
  margin: 0.4rem;
}
.actions .device {
  width: 100%;
  border: 2px solid gray;
  padding: 0.5rem;
  box-sizing: border-box;
}

table, tr {
  width: 100%;
}

tr {
  display: flex;
}

.resource-name {
  flex: 1;
}

.resource-count {
  width: auto;
  font-family: monospace;
}
.resource-count.animated {
  animation: flash 0.4s ease;
}

@keyframes flash {
  0% {
    font-weight: 600;
  }
  100% {
    font-weight: 400;
  }
}
button {
  font-family: sans-serif;
  padding: 0.4rem;
  margin: 0.2rem;
}
button.bigbtn {
  width: 100%;
  height: 3rem;
}
button.bigbtn b {
  font-size: 1rem;
}